.radio-inputs {
    display: grid;
    grid-template-columns: repeat(3, auto);
    justify-content: center;
    align-items: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .radio-inputs > * {
    margin: 6px;
  }
  
  .radio-input:checked + .radio-tile {
    border-color: var(--secondary-color);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    color: var(--secondary-color);
  }
  
  .radio-input:checked + .radio-tile:before {
    transform: scale(1);
    opacity: 1;
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
  }
  
  .radio-input:checked + .radio-tile .radio-icon svg {
    fill: var(--secondary-color);
  }
  
  .radio-input:checked + .radio-tile .radio-label {
    color: var(--secondary-color);
  }
  
  .radio-input:focus + .radio-tile {
    border-color: var(--secondary-color);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1), 0 0 0 4px #b5c9fc;
  }
  
  .radio-input:focus + .radio-tile:before {
    transform: scale(1);
    opacity: 1;
  }
  
  .radio-tile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80px;
    min-height: 80px;
    border-radius: 0.5rem;
    border: 2px solid #b5bfd9;
    background-color: #fff;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    transition: 0.15s ease;
    cursor: pointer;
    position: relative;
  }
  
  .radio-tile:before {
    content: "";
    position: absolute;
    display: block;
    width: 0.75rem;
    height: 0.75rem;
    border: 2px solid #b5bfd9;
    background-color: #fff;
    border-radius: 50%;
    top: 0.25rem;
    left: 0.25rem;
    opacity: 0;
    transform: scale(0);
    transition: 0.25s ease;
  }
  
  .radio-tile:hover {
    border-color: var(--secondary-color);
  }
  
  .radio-tile:hover:before {
    transform: scale(1);
    opacity: 1;
  }
  
  .radio-icon svg {
    width: 2rem;
    height: 2rem;
    fill: #494949;
  }
  
  .radio-label {
    color: #707070;
    transition: 0.375s ease;
    text-align: center;
    font-size: 13px;
  }
  
  .radio-input {
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }



  .shipping__cost{
    display: flex;
    flex-direction: column;
  }
  .shipping__cost input{
    width: 100%;
    height: 2rem;
  }
  .submit{
    border: 1px solid;
    cursor: pointer;
    text-align: center;
    background-color: var(--secondary-color);
    color: #fff;
    border: none;
    border-radius: 6px;
  }

  .submit:hover{
    background-color: var(--primary-color);
    color: var(--secondary-color);
    font-weight: 600;
  }